  position: relative;
  overflow: hidden;
}

.main_l > ul > li:before {
  content: '+';
  width: 20px;
  height: 20px;
  line-height: 18px;
  text-align: center;
  color: #415b87;
  border: 1px solid #415b87;
  position: absolute;
